.info {
    display: grid;
    grid-template-rows: 1fr;
    width: 100%;
    height: 100%;
    margin: 0;
    margin-top: 50px;
}

.info_content {
    display: grid;
    grid-template-columns: 35% 2% 30% 2% 1fr;
    grid-column-gap: 55px;
}

